@import url(https://fonts.googleapis.com/css?family=Roboto:300,700,600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
* {
  box-sizing: border-box; }

body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.2;
  font-size: 1rem;
  min-width: 320px;
  margin: 0;
  padding: 0; }

h1.main-heading,
p.sub-heading {
  text-align: center; }

h1.main-heading {
  font-size: 1.8em; }
  @media only screen and (min-width: 480px) {
    h1.main-heading {
      font-size: 3em; } }

.todo-container {
  background-color: whitesmoke; }
  .todo-container .main-heading {
    font-family: 'Permanent Marker', cursive;
    margin-bottom: 0.5em; }
  .todo-container .sub-heading {
    font-size: 1em;
    margin: 0 0 1.7em 0; }
    @media only screen and (min-width: 480px) {
      .todo-container .sub-heading {
        font-size: 1.5em; } }

.todo-container {
  margin: 50px auto;
  padding: 15px 0px;
  width: 90%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  border: 1px solid lightgrey;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.31); }

.add-todo {
  display: flex;
  padding: 20px;
  background-color: purple;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.31);
  margin: 0; }
  .add-todo input[type='text'] {
    padding: 0.6em 0.5em;
    font-size: 1em;
    flex: 1 1 90%; }
  .add-todo button {
    border: none;
    font-size: 1.5em;
    flex: 1 1 10%;
    -webkit-appearance: none;
    background-color: white;
    margin: 0;
    padding: 0;
    border-radius: 0; }

.filter-list {
  margin: 2em auto;
  list-style-type: none;
  display: flex;
  padding: 0;
  font-size: 0.7em;
  font-weight: 600;
  text-transform: uppercase;
  justify-content: center; }
  .filter-list li {
    padding: 0 10px;
    opacity: 0.5;
    cursor: pointer; }
  .filter-list li.active {
    color: #1a73e8;
    font-weight: bold;
    opacity: 1; }

.todo-list::before,
.todo-list::after {
  content: '';
  width: 75%;
  height: 1px;
  margin: 0 auto;
  background-color: lightgrey; }

.no-todos {
  text-align: center;
  color: grey;
  padding: 2em 0; }

.todo-list {
  display: flex;
  flex-direction: column;
  padding: 0.1em 0; }
  .todo-list .todo-item {
    margin-left: 20%;
    margin-top: 20px;
    display: flex;
    align-items: center;
    text-align: center; }
    @media only screen and (min-width: 480px) {
      .todo-list .todo-item {
        margin-left: 30%;
        font-size: 1.2rem; } }
  .todo-list input[type='checkbox'] {
    display: none; }
  .todo-list .checked::before,
  .todo-list .not-checked::before {
    height: 10px;
    width: 10px;
    margin: 0 0.7em 1.4em;
    padding-top: 0.2em;
    font-size: 1.5em; }
  .todo-list .checked::before {
    content: '\1F63B';
    color: green; }
  .todo-list .not-checked::before {
    content: '\1F63E';
    color: red; }
  .todo-list .checked label {
    opacity: 0.4;
    text-decoration: line-through; }
  .todo-list .todo-item label {
    margin-left: 15px;
    padding-bottom: 0.8em;
    text-transform: uppercase; }
  .todo-list .todo-item label:hover {
    font-weight: 700; }
  .todo-list .todo-item label:active {
    font-weight: 400; }
  .todo-list .remove-todo {
    color: red;
    font-weight: 900;
    display: inline-block;
    margin-left: 0.5em;
    padding-bottom: 0.8em; }
    @media only screen and (min-width: 1024px) {
      .todo-list .remove-todo {
        opacity: 0; } }
  .todo-list .remove-todo:hover {
    opacity: 1; }

.stats {
  display: flex;
  justify-content: space-around;
  text-align: center; }

